"use client";

import React from 'react';
import { Card } from '@/components/ui/card';
import Button from '@/components/ui/button/Button';
import { Notificacao } from '@/services/api/notificacao.service';
import { formatDate } from '@/utils/format';
import { TrashBinIcon, EyeIcon, CheckCircleIcon } from '@/icons';
import { useAuth } from '@/hooks/useAuth';

interface NotificacaoCardProps {
  notificacao: Notificacao;
  onMarcarComoLida: (id: number) => void;
  onDelete: (id: number) => void;
  onView?: (id: number) => void;
}

function formatTimeAgo(dateString: string): string {
  try {
    const date = new Date(dateString);
    const now = new Date();
    const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000);

    if (diffInSeconds < 60) return 'agora';
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)} min atr√°s`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)} hr atr√°s`;
    if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)} dias atr√°s`;
    
    return formatDate(dateString);
  } catch {
    return dateString;
  }
}

function getTipoIcon(tipo: string): string {
  switch (tipo) {
    case 'VENCIMENTO_EMPRESA':
      return '‚ö†Ô∏è';
    case 'PLANO_PAGO':
      return '‚úÖ';
    case 'COBRANCA_GERADA':
      return 'üí≥';
    case 'FATURA_PAGA':
      return 'üí∞';
    case 'PARCELA_PAGA':
      return 'üíµ';
    case 'MANUAL':
      return 'üì¢';
    case 'TICKET_MENSAGEM':
      return 'üé´';
    default:
      return 'üîî';
  }
}

function getTipoLabel(tipo: string): string {
  switch (tipo) {
    case 'VENCIMENTO_EMPRESA':
      return 'Vencimento de Empresa';
    case 'PLANO_PAGO':
      return 'Plano Pago';
    case 'COBRANCA_GERADA':
      return 'Cobran√ßa Gerada';
    case 'FATURA_PAGA':
      return 'Fatura Paga';
    case 'PARCELA_PAGA':
      return 'Parcela Paga';
    case 'MANUAL':
      return 'Manual';
    case 'TICKET_MENSAGEM':
      return 'Ticket';
    default:
      return tipo;
  }
}

export default function NotificacaoCard({
  notificacao,
  onMarcarComoLida,
  onDelete,
  onView
}: NotificacaoCardProps) {
  const { user } = useAuth();
  const isSuperAdmin = user?.role === 'SUPERADMIN';

  return (
    <Card className={`p-4 transition-all hover:shadow-md ${
      !notificacao.lida ? 'border-l-4 border-l-blue-500 bg-blue-50/50 dark:bg-blue-900/10' : ''
    }`}>
      <div className="flex items-start gap-4">
        <div className="flex-shrink-0">
          <div className="w-12 h-12 rounded-full bg-gray-100 dark:bg-gray-800 flex items-center justify-center text-2xl">
            {getTipoIcon(notificacao.tipo)}
          </div>
        </div>

        <div className="flex-1 min-w-0">
          <div className="flex items-start justify-between gap-2 mb-2">
            <div className="flex-1">
              <h3 className={`text-base font-semibold text-gray-900 dark:text-white mb-1 ${
                !notificacao.lida ? 'font-bold' : ''
              }`}>
                {notificacao.titulo}
              </h3>
              <div className="flex items-center gap-2 flex-wrap mb-2">
                <span className="px-2 py-0.5 text-xs font-medium rounded-full bg-gray-100 dark:bg-gray-800 text-gray-700 dark:text-gray-300">
                  {getTipoLabel(notificacao.tipo)}
                </span>
                {!notificacao.lida && (
                  <span className="px-2 py-0.5 text-xs font-medium rounded-full bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300">
                    N√£o lida
                  </span>
                )}
              </div>
            </div>
          </div>

          <p className="text-sm text-gray-600 dark:text-gray-400 mb-3 line-clamp-3">
            {notificacao.mensagem}
          </p>

          {/* Mostrar empresa e usu√°rio apenas para SUPERADMIN */}
          {isSuperAdmin && (notificacao.empresa || notificacao.user) && (
            <div className="mb-3 p-2 bg-gray-50 dark:bg-gray-800/50 rounded-lg">
              <div className="flex flex-wrap gap-2 text-xs text-gray-600 dark:text-gray-400">
                {notificacao.empresa && (
                  <span className="flex items-center gap-1">
                    <span>üì¶</span>
                    <span className="font-medium">Empresa:</span>
                    <span>{notificacao.empresa.nome}</span>
                  </span>
                )}
                {notificacao.user && (
                  <span className="flex items-center gap-1">
                    <span>üë§</span>
                    <span className="font-medium">Usu√°rio:</span>
                    <span>{notificacao.user.nome}</span>
                  </span>
                )}
              </div>
            </div>
          )}

          <div className="flex items-center justify-between text-xs text-gray-500 dark:text-gray-400 mb-3">
            <span>{formatTimeAgo(notificacao.createdAt)}</span>
            {notificacao.actionLink && notificacao.actionLabel && (
              <a
                href={notificacao.actionLink}
                className="text-brand-500 hover:text-brand-600 dark:text-brand-400 font-medium"
              >
                {notificacao.actionLabel} ‚Üí
              </a>
            )}
          </div>

          <div className="flex items-center gap-2 pt-3 border-t border-gray-200 dark:border-gray-700">
            {!notificacao.lida && (
              <Button
                variant="outline"
                size="sm"
                onClick={() => onMarcarComoLida(notificacao.id)}
                startIcon={<CheckCircleIcon className="w-5 h-5" />}
                aria-label="Marcar como lida"
              >
                Marcar como lida
              </Button>
            )}
            {onView && (
              <Button
                variant="outline"
                size="sm"
                onClick={() => onView(notificacao.id)}
                startIcon={<EyeIcon className="w-5 h-5" />}
                aria-label="Ver detalhes"
              >
                Ver
              </Button>
            )}
            <Button
              variant="outline"
              size="sm"
              onClick={() => onDelete(notificacao.id)}
              startIcon={<TrashBinIcon className="w-5 h-5" />}
              aria-label="Excluir notifica√ß√£o"
              className="text-red-600 hover:text-red-700 dark:text-red-400 dark:hover:text-red-300"
            >
              Excluir
            </Button>
          </div>
        </div>
      </div>
    </Card>
  );
}

