'use client';

import React, { useState, useEffect, useCallback } from 'react';
import { toast } from 'react-hot-toast';
import { Card } from '@/components/ui/card';
import Input from '@/components/form/input/InputField';
import Select, { SelectOption } from '@/components/ui/Select';
import Button from '@/components/ui/button/Button';
import Pagination from '@/components/common/Pagination';
import Label from '@/components/form/Label';
import { services } from '@/services/api';
import type { Notificacao } from '@/services/api/notificacao.service';
import NotificacaoCard from '@/components/notificacoes/NotificacaoCard';
import NotificacaoModal from '@/components/notificacoes/NotificacaoModal';
import { useNotifications } from '@/hooks/useNotifications';
import { useAuth } from '@/hooks/useAuth';
import { ThemeToggleButton } from '@/components/common/ThemeToggleButton';
import { BellIcon } from '@/icons';

type FilterOptions = {
  lida?: boolean;
  tipo?: Notificacao['tipo'];
};

export default function NotificacoesPage() {
  const { user } = useAuth();
  const { marcarComoLida, marcarTodasComoLidas, deleteNotificacao } = useNotifications();
  const [notificacoes, setNotificacoes] = useState<Notificacao[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [filters, setFilters] = useState<FilterOptions>({});
  const [itemsPerPage, setItemsPerPage] = useState(20);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedNotificacao, setSelectedNotificacao] = useState<Notificacao | null>(null);

  const loadNotificacoes = useCallback(async () => {
    try {
      setLoading(true);
      const params: any = {
        page,
        perPage: itemsPerPage
      };

      if (searchTerm) {
        // Buscar por t√≠tulo ou mensagem (se o backend suportar)
        // Por enquanto, vamos filtrar no frontend
      }

      if (filters.lida !== undefined) {
        params.lida = filters.lida;
      }

      if (filters.tipo) {
        params.tipo = filters.tipo;
      }

      const response = await services.notificacao.list(params);
      setNotificacoes(response.data || []);
      setTotalPages(response.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Erro ao carregar notifica√ß√µes:', error);
      toast.error('Erro ao carregar notifica√ß√µes');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, filters, itemsPerPage]);

  useEffect(() => {
    loadNotificacoes();
  }, [loadNotificacoes]);

  // Filtrar por termo de busca no frontend (j√° que o backend pode n√£o suportar)
  const filteredNotificacoes = notificacoes.filter(notif => {
    if (!searchTerm) return true;
    const search = searchTerm.toLowerCase();
    return (
      notif.titulo.toLowerCase().includes(search) ||
      notif.mensagem.toLowerCase().includes(search)
    );
  });

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(1);
  };

  const handleFilterChange = (newFilters: Partial<FilterOptions>) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
    setPage(1);
  };

  const handleMarcarComoLida = async (id: number) => {
    try {
      await marcarComoLida(id);
      await loadNotificacoes();
      toast.success('Notifica√ß√£o marcada como lida');
    } catch (error) {
      toast.error('Erro ao marcar notifica√ß√£o como lida');
    }
  };

  const handleMarcarTodasComoLidas = async () => {
    try {
      await marcarTodasComoLidas();
      await loadNotificacoes();
      toast.success('Todas as notifica√ß√µes foram marcadas como lidas');
    } catch (error) {
      toast.error('Erro ao marcar todas como lidas');
    }
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta notifica√ß√£o?')) return;

    try {
      await deleteNotificacao(id);
      await loadNotificacoes();
      toast.success('Notifica√ß√£o exclu√≠da com sucesso');
    } catch (error) {
      toast.error('Erro ao excluir notifica√ß√£o');
    }
  };

  const handleView = (id: number) => {
    const notificacao = notificacoes.find(n => n.id === id);
    if (notificacao) {
      setSelectedNotificacao(notificacao);
      setIsModalOpen(true);
    }
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedNotificacao(null);
    // Recarregar notifica√ß√µes para atualizar status se foi marcada como lida
    loadNotificacoes();
  };

  const tipoOptions: SelectOption[] = [
    { value: 'MANUAL', label: 'Manual' },
    { value: 'VENCIMENTO_EMPRESA', label: 'Vencimento de Empresa' },
    { value: 'PLANO_PAGO', label: 'Plano Pago' },
    { value: 'COBRANCA_GERADA', label: 'Cobran√ßa Gerada' },
    { value: 'FATURA_PAGA', label: 'Fatura Paga' },
    { value: 'PARCELA_PAGA', label: 'Parcela Paga' },
    { value: 'TICKET_MENSAGEM', label: 'Ticket Mensagem' }
  ];

  const lidaOptions: SelectOption[] = [
    { value: 'true', label: 'Lidas' },
    { value: 'false', label: 'N√£o lidas' }
  ];

  const countNaoLidas = notificacoes.filter(n => !n.lida).length;

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Notifica√ß√µes
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie todas as suas notifica√ß√µes
            </p>
          </div>
          <div className="flex items-center gap-2">
            {countNaoLidas > 0 && (
              <Button
                variant="outline"
                onClick={handleMarcarTodasComoLidas}
                aria-label="Marcar todas como lidas"
              >
                Marcar todas como lidas
              </Button>
            )}
            {/* <ThemeToggleButton /> */}
          </div>
        </div>

        <div className="mt-6 flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar notifica√ß√µes..."
              value={searchTerm}
              onChange={handleSearch}
              className="w-full"
              aria-label="Buscar notifica√ß√µes"
            />
          </div>
          <div className="flex flex-wrap gap-3">
            <Select
              placeholder="Status"
              options={lidaOptions}
              value={filters.lida !== undefined ? lidaOptions.find(opt => opt.value === String(filters.lida)) : undefined}
              onChange={(option) => handleFilterChange({ 
                lida: option ? (option as SelectOption).value === 'true' : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-40"
              aria-label="Filtrar por status"
            />
            <Select
              placeholder="Tipo"
              options={tipoOptions}
              value={filters.tipo ? tipoOptions.find(opt => opt.value === filters.tipo) : undefined}
              onChange={(option) => handleFilterChange({ 
                tipo: option ? ((option as SelectOption).value as Notificacao['tipo']) : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-50"
              aria-label="Filtrar por tipo"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className="flex items-center gap-2">
          <Label className="flex items-center mb-0 h-full">Quantidade por p√°gina</Label>
          <Input
            className="max-w-24"
            type="number"
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 20;
              setItemsPerPage(newValue);
              setPage(1);
            }}
            aria-label="Itens por p√°gina"
          />
        </div>
        <Pagination
          currentPage={page}
          totalPages={totalPages}
          onPageChange={setPage}
        />
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : filteredNotificacoes.length === 0 ? (
        <Card className="p-12 text-center">
          <BellIcon className="w-16 h-16 mx-auto text-gray-400 mb-4" />
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
            Nenhuma notifica√ß√£o encontrada
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            {searchTerm || filters.lida !== undefined || filters.tipo
              ? 'Tente ajustar os filtros de busca'
              : 'Voc√™ n√£o possui notifica√ß√µes no momento'}
          </p>
        </Card>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {filteredNotificacoes.map((notificacao) => (
            <NotificacaoCard
              key={notificacao.id}
              notificacao={notificacao}
              onMarcarComoLida={handleMarcarComoLida}
              onDelete={handleDelete}
              onView={handleView}
            />
          ))}
        </div>
      )}

      {!loading && filteredNotificacoes.length > 0 && (
        <div className="mt-6 flex justify-center">
          <Pagination
            currentPage={page}
            totalPages={totalPages}
            onPageChange={setPage}
          />
        </div>
      )}

      {/* Modal de visualiza√ß√£o */}
      <NotificacaoModal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        notificacao={selectedNotificacao}
        onMarcarComoLida={handleMarcarComoLida}
        onDelete={handleDelete}
      />
    </div>
  );
}

